@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  body {
    @apply bg-[#1d1e1e];
  }

  @font-face {
    font-family: "Rosaliya";
    src: local("Rosaliya"), url("./fonts/Rosaliya.ttf") format("truetype");
  }
  @font-face {
    font-family: "Affectionately";
    src: local("Affectionately"),
      url("./fonts/Affectionately_Yours_-_TTF.ttf") format("truetype");
  }
}

@layer components {
  .fade-in {
    animation: fadeIn 2s ease-in-out;
  }
  .fade-out {
    animation: fadeOut 2s ease-in-out;
  }

  @keyframes fadeIn {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
  }

  @keyframes fadeOut {
    0% {
      opacity: 1;
    }
    100% {
      opacity: 0;
    }
  }

  body::-webkit-scrollbar {
    width: 10px;
  }
  body::-webkit-scrollbar-track {
    background-color: #1d1e1e;
  }
  body::-webkit-scrollbar-thumb {
    background-color: #4e4e4e;
    border-radius: 50px;
  }

  .prompt::-webkit-scrollbar {
    width: 10px;
  }
  .prompt::-webkit-scrollbar-track {
    @apply bg-ctp-base;
  }
  .prompt::-webkit-scrollbar-thumb {
    @apply bg-ctp-crust rounded-[50px];
  }
}
